:root {
    --primary: #444EF2;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 2px solidd var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #444EF2;
    padding: 8px 20px;
    border: 2px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--landing {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 2px solid #fff;
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover,
.btn--large:hover {
    background: #444EF2;
    color: #242424;
    transition: all 0.3s ease-out;
}

.btn--landing:hover {
    background: #fff;
    color: #242424;
    transition: all 0.3s ease-out;
}